import React, { Component } from 'react'
import PropTypes from 'prop-types'
import Link from 'gatsby-link'
import Helmet from 'react-helmet'

import logo from '../assets/images/logo.png';
import desktop from '../assets/images/desktop.png';
import toggles from '../assets/images/toggles.png';
import trophy from '../assets/images/trophy.png';

class WorkComponent extends Component {
    render() {
        return (
            <section className="row how-it-works" id="process">
                <div className="container">
                    <div className="row section-header v2 wow fadeInUp">
                        <h2>how it works</h2>
                        <p>DuBase help you build better apps to grow your business in just simple steps.</p>
                    </div>
                    <div className="row work-processes">

                        <div className="col-sm-4 work-process wow fadeIn">
                            <div className="row m0 process-icon">
                                <img src={desktop} alt="" />
                            </div>
                            <h3>Install</h3>
                            <p>Lorem ipsum dolor sit amet, consectetur de elit, sed do tempor incididunt ut labore eta rehenderit in
                        voluptate velit.</p>
                        </div>

                        <div className="col-sm-4 work-process wow fadeIn" data-wow-delay="0.5s">
                            <div className="row m0 process-icon">
                                <img src={toggles} alt="" />
                            </div>
                            <h3>Configure it</h3>
                            <p>Lorem ipsum dolor sit amet, consectetur de elit, sed do tempor incididunt ut labore eta rehenderit in
                        voluptate velit.</p>
                        </div>

                        <div className="col-sm-4 work-process wow fadeIn" data-wow-delay="1s">
                            <div className="row m0 process-icon">
                                <img src={trophy} alt="" />
                            </div>
                            <h3>Yay! Done.</h3>
                            <p>Lorem ipsum dolor sit amet, consectetur de elit, sed do tempor incididunt ut labore eta rehenderit in
                        voluptate velit.</p>
                        </div>
                    </div>
                </div>
            </section>
        );
    }
}

export default WorkComponent;


